import send from './http';
export default {
    getFleetList(data) {
        let req = {
            data: data,
            action: 'ticket.getFleetList'
        };
        return send.fetch(req, 'post');
    },
    getCarList(data) {
        let req = {
            data: data,
            action: 'ticket.getCarList'
        };
        return send.fetch(req, 'post');
    },
    getCarInfo(data) {
        let req = {
            data: data,
            action: 'ticket.getCarInfo'
        };
        return send.fetch(req, 'post');
    },
    getOrderList(data) {
        let req = {
            data: data,
            action: 'ticket.getOrderList'
        };
        return send.fetch(req, 'post');
    },
    orderAllocation(data) {
        let req = {
            data: data,
            action: 'ticket.orderAllocation'
        };
        return send.fetch(req, 'post');
    },
    addOrUpdateCar(data) {
        let req = {
            data: data,
            action: 'ticket.addOrUpdateCar'
        };
        return send.fetch(req, 'post');
    },
    sendSMS(data) {
        let req = {
            data: data,
            action: 'ticket.sendSMS'
        };
        return send.fetch(req, 'post');
    },
    qrValidate(data) {
        let req = {
            data: data,
            action: 'qrValidate'
        };
        return send.fetch(req, 'post');
    }
};
