import store from '../index';
const URL = process.env.NODE_ENV !== 'production' ? '/action' : '/mtaction';
import router from '../../routers';
function fetchByPost(action, noSession) {
    let sessionId = store.state.sessionId;
    let data = {
        data: JSON.stringify(action.data),
        action: action.action,
        sessionId: ''
    };
    data.sessionId = action.action !== 'login' ? sessionId : '';
    if (noSession) {
        data.sessionId = null;
    }
    return window.fetch(action.url, {
        method: action.method || 'post',
        body: JSON.stringify(data),
        headers: {
            'Content-Type': 'application/json'
        }
    }).then((res) => {
        return commonThen(res);
    }).catch((err) => {
        console.log(err);
        return false;
    });
}
function fetchByGet(action) {
    let sessionId = store.state.sessionId;
    let data = {
        data: JSON.stringify(action.data),
        action: action.action,
        sessionId: ''
    };
    console.log(action.action);
    data.sessionId = action.action !== 'loginToAuthCenter' ? sessionId : '';
    let url = action.url + '?action=' + data.action + '&data= ' + data.data + '&sessionId=' + data.sessionId;
    return window.fetch(url, {
        method: action.method || 'get',
        headers: {
            'Content-Type': 'application/json'
        }
    }).then((res) => {
    }).catch((res) => {
    });
}
function commonThen(res) {
    let response = res.json();
    response.then(function (data) {
        if (data.result === 401 || (data.result === 403 && !window.localStorage.sessionId) || data.sessionExpire) {
            window.localStorage.removeItem('sessionId');
            router.push('/login');
        }
    });
    // status code 2xx正常ruturn 其它走catch
    if (res.status.toString()[0] === '2') {
        return response;
    }
    else {
        return Promise.reject(res);
    }
}
export default {
    fetch(data, method, hiddenLoading, noSession) {
        let action = data;
        action.url = URL;
        return method === 'post' ? fetchByPost(action, noSession) : fetchByGet(action);
    }
};
