import * as types from './mutation-types';
import Vue from 'vue';
export default {
    [types.SET_TOKEN](state, sessionId) {
        state.token = sessionId;
        window.localStorage.setItem('sessionId', sessionId);
    },
    [types.SET_USER_INFO](state, userInfo) {
        Vue.set(state, 'userInfo', userInfo);
    },
    [types.SET_SESSION_ID](state, sessionId) {
        state.sessionId = sessionId;
    },
    [types.SET_HOUSE_LIST](state, houseList) {
        Vue.set(state, 'houseList', houseList);
    },
    [types.COMMODITY_USER_INFO](state, commodityUserInfo) {
        state.isUserInfoBack = true;
        Vue.set(state, 'commodityUserInfo', commodityUserInfo);
    },
    [types.SET_SELECT_COMMODITY_LIST](state, { type, item }) {
        let arr = [];
        let id = item['id'];
        let isExist = false;
        state.selectCommodityList.forEach((obj) => {
            if (obj.id === item.id) {
                isExist = true;
                obj.num = item['num'];
                if (type === 'add') {
                    arr.push(obj);
                }
                else {
                    if (obj.num > 0) {
                        arr.push(obj);
                    }
                }
            }
            else {
                arr.push(obj);
            }
        });
        if (type === 'add') {
            if (!isExist) {
                arr.push(item);
            }
        }
        Vue.set(state, 'selectCommodityList', arr);
    },
    [types.SET_SELECT_COMMODITY_NUM](state, { classId, type }) {
        let num = state.selectNumClass[classId] || 0;
        let setNum = 0;
        type === 'add' ? setNum = num + 1 : setNum = num - 1;
        Vue.set(state.selectNumClass, classId, setNum);
    },
    [types.EMPTY_COMMODITY](state) {
        Vue.set(state, 'selectCommodityList', []);
        Vue.set(state, 'selectNumClass', {});
        Object.keys(state.classCommodity).forEach((key) => {
            // Vue.set(state.classCommodity[key])
            state.classCommodity[key].forEach((item, index) => {
                Vue.set(state.classCommodity[key][index], 'num', 0);
            });
        });
    },
    [types.SET_CLASS_COMMODITY](state, { classId, list, isEmpty }) {
        if (isEmpty) {
            Vue.set(state, 'classCommodity', {});
        }
        else {
            Vue.set(state.classCommodity, classId, list);
        }
    },
    [types.SET_BUSINESS_ID](state, businessId) {
        state.businessId = businessId;
    },
    [types.SET_MTUSER_ID](state, userId) {
        state.mtUserId = userId;
    },
    [types.SET_SHOP_INFO](state, info) {
        state.shopInfo = info;
    },
    [types.SET_SCHOOL_INFO](state, info) {
        Vue.set(state, 'schoolInfo', info);
    },
    [types.SET_SCHOOL_INFO_BY_TYPE](state, { type, value }) {
        Vue.set(state.schoolInfo, type, value);
    }
};
