import Vue from 'vue';
import Router from 'vue-router';
import { routers } from './router';
import store from '../store/index';
const whiteList = ['/inviteadmin', '/memetest'];
const whiteNameList = ['inviteadmin'];
Vue.use(Router);
const router = new Router({
    mode: 'history',
    base: '/mt',
    routes: routers
});
router.beforeEach((to, from, next) => {
    console.log(to, 'tooooooooooooooooooooooooo');
    if ((whiteList.indexOf(to.path) > -1 || whiteNameList.indexOf(to.name) > -1) || store.state.sessionId && to.name !== 'login') {
        next();
    }
    else {
        // other pages that do not have permission to access are redirected to the login page.
        if (to.name === 'login') {
            next();
        }
        else {
            next(`/login?redirect=${to.path}`);
        }
    }
    setTitle(to.meta.title);
});
export function setTitle(title) {
    title = title || '';
    window.document.title = title;
}
export default router;
