import * as types from './mutation-types';
import loginApi from './api/login';
import bus from './modules/bus/index';
import store from './index';
import router from '../routers/index';
import { Toast } from 'cube-ui';
export function initVuexModules(store) {
    !store.state.bus && store.registerModule('bus', bus);
}
export const login = ({ commit, state, rootState, dispatch }, data) => {
    return loginApi.login(data).then((res) => {
        if (res.success) {
            if (res.sessionId) {
                commit(types.SET_TOKEN, res.sessionId);
                initVuexModules(store);
                router.replace('home');
                return { success: true };
            }
            else {
                return { success: false };
            }
        }
        else {
            return { success: false };
        }
    });
};
export const getCodeInfo = ({ commit, state, rootState, dispatch }, data) => {
    return loginApi.getCodeInfo(data).then((res) => {
        if (res.success) {
            return { success: true, data: res.data };
        }
        else {
            return { success: false };
        }
    });
};
