// 校园后台管理
export const updateSchoolInfo = ({ commit, state, rootState, dispatch }, data) => {
    const toast = Toast.$create({
        txt: '保存中...',
        mask: true
    });
    toast.show();
    return loginApi.updateSchoolInfo(data).then((res) => {
        toast.hide();
        if (res.success) {
            return { success: true, data: res.data };
        }
        else {
            return res;
        }
    });
};
